// src/components/ReservasForm.jsx
"use client";
import { useState, useEffect } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { Container, Form, Button, Alert } from "react-bootstrap";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();
  const [servicio, setServicio] = useState("");
  const [terapeuta, setTerapeuta] = useState("");
  const [fecha, setFecha] = useState(null);

  useEffect(() => {
    if (status === "unauthenticated") router.replace("/login");
  }, [status, router]);

  const handleSubmit = (e) => {
    e.preventDefault();
    // tu llamada a API → luego redirigir a Stripe checkout
  };

  if (status !== "authenticated") return null;

  return (
    <Form onSubmit={handleSubmit} style={{ maxWidth: 600, margin: "0 auto" }}>
      <Form.Group className="mb-3">
        <Form.Label>Servicio</Form.Label>
        <Form.Select value={servicio} onChange={e => setServicio(e.target.value)} required>
          <option value="">Selecciona un servicio</option>
          {/* ... tus <option> */}
        </Form.Select>
      </Form.Group>

      <Form.Group className="mb-3">
        <Form.Label>Terapeuta</Form.Label>
        <Form.Select value={terapeuta} onChange={e => setTerapeuta(e.target.value)} required>
          <option value="">Selecciona un terapeuta</option>
          {/* ... */}
        </Form.Select>
      </Form.Group>

      <Form.Group className="mb-4">
        <Form.Label>Fecha y hora</Form.Label>
        <Calendar onChange={setFecha} value={fecha} minDate={new Date()} />
        {fecha && (
          <Alert variant="info" className="mt-2">
            Has seleccionado: {fecha.toLocaleString()}
          </Alert>
        )}
      </Form.Group>

      <Button type="submit" className="btn-orange w-100">
        Confirmar y pagar
      </Button>
    </Form>
  );
}
